.about_us {
  display: flex;
  flex-direction: column;
}
.container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  gap: 20px;

  flex: 1;
  min-height: 100vh;

  margin: 0 auto;
  padding: 50px 15px;
  .title {
    font-size: 36px;
    font-weight: 700;
    line-height: 1.3;
    letter-spacing: 0.03em;
    text-align: center;
  }
  .discription {
    max-width: 1200px;

    font-size: 16px;
    font-weight: 400;
    line-height: 1.7;
    letter-spacing: 0.05em;
    text-align: left;
  }
}
